.header {
  position: relative;
  border: 1px solid gray;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.5rem 1rem 1.5rem;
  height: 250px;
  width: 100%;
  background-image: url('../../assets/background.jpg');
}

.title h2 {
  text-align: center;
  margin-top: 4rem;
  height: 4rem;
  font-family: "Ubuntu", sans-serif;
  font-weight: 700;
  font-style: italic;
  color: #e8cbcb;
}

.search {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 1.5rem;
}

.search Input {
  width: 40%;
  height: 2.5rem;
  border: 2px solid black;
  background: #e8e3cd;
}

.search Button {
  width: 10%;
  height: 2.5rem;
  border: 2px solid black;
  background-color: #3e5962;

  &:hover{
    background-color: #192d33;
  }
}
